___CSS_LOADER_EXPORT___.push([module.id, "\n.studio-header .menuItem i {\n  font-size: 20px;\n  margin-right: 8px;\n}\n.studio-header .group-image {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  border-radius: 99px;\n  background: var(--Plateau-Navy-lighten-6, #F5F5FA);\n  height: 30px;\n  width: 30px;\n  margin-right: 12px;\n}\n.studio-header .group-image img {\n  height: 30px;\n}\n.organization-div .group-cancel-icon {\n  color: var(--Plateau-Navy, #151233);\n  text-align: center;\n  font-family: Material Design Icons;\n  font-size: 20px;\n  font-style: normal;\n  font-weight: 400;\n  line-height: normal;\n}\n.organization-div .group-check-icon {\n  color: var(--Grey-darken-2, #757575);\n  text-align: center;\n  font-family: Material Design Icons;\n  font-size: 16px;\n  font-style: normal;\n  font-weight: 400;\n  line-height: normal;\n}\n.organization-div .manage-section {\n  display: flex;\n}\n.organization-div .premium-account-tooltip {\n  visibility: hidden;\n  opacity: 0;\n  top: 122px;\n  right: 200px;\n}\n.organization-div .org-group-ownership {\n  margin-left: 16px;\n  color: var(--Grey-darken-1, #A1A1A1);\n  font-family: Roboto;\n  font-size: 15px;\n  font-style: normal;\n  font-weight: 400;\n  line-height: normal;\n}\n.organization-div .breadcrumb-items {\n  color: var(--Grey-darken-3, #616161);\n  font-family: Roboto;\n  font-size: 15px;\n  font-style: normal;\n  font-weight: 400;\n  line-height: normal;\n  margin-right: 16px;\n}\n.organization-div .home-link {\n  color: var(--Plateau-Navy, #151233);\n  font-family: Roboto;\n  font-size: 15px;\n  font-style: normal;\n  font-weight: 400;\n  line-height: normal;\n  text-decoration-line: underline;\n  cursor: pointer;\n}\n.invite-button {\n  background: #2C5A86;\n  border-radius: 4px;\n  border: none;\n  padding: 8px 16px;\n  font-size: 15px;\n  font-weight: 500;\n  color: #FFFFFF;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  height: 36px;\n  margin-right: 12px;\n}\n.select-organization {\n  width: 260px;\n  font-weight: 500;\n  font-size: 15px;\n  height: 36px;\n  line-height: 36px;\n}\n.manage-button {\n  border: 1px solid #151233;\n  border-radius: 4px;\n  padding: 8px 16px;\n  font-size: 15px;\n  font-weight: 500;\n  color: #151233;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  height: 36px;\n  background-color: transparent;\n}\n.organization-div .custom-select {\n  width: 260px !important;\n}\n.selected-org {\n  background-color: transparent;\n  border-radius: 4px;\n  border: 1px solid #BCBCBC;\n  color: #212121;\n  padding-left: 12px;\n}\n.selected-org i {\n  font-size: 20px;\n  color: #BCBCBC;\n}\n.create-organization-button {\n  background: #2C5A86;\n  border-radius: 4px;\n  border: none;\n  padding: 8px 16px;\n  font-size: 15px;\n  font-weight: 500;\n  color: #FFFFFF;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  height: 36px;\n  justify-content: center;\n}\n.organization-div {\n  border-bottom: 1px solid #E3E3E3;\n  padding: 14px 24px;\n  display: flex;\n  align-items: center;\n  background: #fff;\n  padding-left: 36px;\n  justify-content: space-between;\n}\n.user-info-dropwdown {\n  width: 246px;\n  border: 1px solid #E3E3E3;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  padding-bottom: 10px;\n}\n.menuItem:hover {\n  background-color: #d5d5d5 !important;\n  cursor: pointer;\n}\n.menuItem {\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  display: flex;\n  align-items: center;\n  padding: 8px 16px 8px 12px;\n}\n.studio-header .user-info {\n  display: flex;\n  align-items: center;\n  padding: 16px 16px 16px 12px;\n}\n.user-name {\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  margin-bottom: 8px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n.user-email {\n  font-size: 14px;\n  line-height: 16px;\n  color: #757575;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n.chevronIcon {\n  cursor: pointer;\n}\n.documantationsText {\n  margin-top: 28px;\n  color: #212121;\n  font-family: \"Roboto\";\n  cursor: pointer;\n  text-decoration: none;\n  font-size: 17px;\n  font-weight: 500;\n}\n.out-link-text {\n  color: #212121;\n  text-decoration: none;\n}\n.studio-header .header {\n  min-height: 72px;\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border-bottom: 1px solid #E3E3E3;\n  display: flex;\n  align-items: center;\n  padding-left: 10px;\n}\n.invitation-row {\n    display: flex;\n    justify-content: space-between;\n    margin: 0 -33px;\n    background-color: #f5f5f5;\n    padding: 10px 57px;\n    border-bottom: 1px solid #D5D5D5;\n    align-items: center;\n}\n.invitation-label {\n    font-weight: bold;\n    margin-bottom: 5px;\n}\n.invitation-value {\n    color: blue;\n}\n.invitation-respond-button {\n    background: transparent;\n    border: none;\n    padding: 7px 16px;\n    border-radius: 4px;\n    font-weight: 500;\n    font-size: 14px;\n    color: #151233;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/main/header.vue"],"names":[],"mappings":";AA6bA;EACE,eAAe;EACf,iBAAiB;AACnB;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,mBAAmB;EACnB,kDAAkD;EAClD,YAAY;EACZ,WAAW;EACX,kBAAkB;AACpB;AAEA;EACE,YAAY;AACd;AAEA;EACE,mCAAmC;EACnC,kBAAkB;EAClB,kCAAkC;EAClC,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;AACrB;AAEA;EACE,oCAAoC;EACpC,kBAAkB;EAClB,kCAAkC;EAClC,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;AACrB;AAEA;EACE,aAAa;AACf;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,UAAU;EACV,YAAY;AACd;AAEA;EACE,iBAAiB;EACjB,oCAAoC;EACpC,mBAAmB;EACnB,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;AACrB;AAEA;EACE,oCAAoC;EACpC,mBAAmB;EACnB,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;EACnB,kBAAkB;AACpB;AAEA;EACE,mCAAmC;EACnC,mBAAmB;EACnB,eAAe;EACf,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;EACnB,+BAA+B;EAC/B,eAAe;AACjB;AAEA;EACE,mBAAmB;EACnB,kBAAkB;EAClB,YAAY;EACZ,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,YAAY;EACZ,kBAAkB;AACpB;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,eAAe;EACf,YAAY;EACZ,iBAAiB;AACnB;AAIA;EACE,yBAAyB;EACzB,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,YAAY;EACZ,6BAA6B;AAC/B;AAEA;EACE,uBAAuB;AACzB;AAEA;EACE,6BAA6B;EAC7B,kBAAkB;EAClB,yBAAyB;EACzB,cAAc;EACd,kBAAkB;AACpB;AAEA;EACE,eAAe;EACf,cAAc;AAChB;AAEA;EACE,mBAAmB;EACnB,kBAAkB;EAClB,YAAY;EACZ,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,YAAY;EACZ,uBAAuB;AACzB;AAEA;EACE,gCAAgC;EAChC,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,gBAAgB;EAChB,kBAAkB;EAClB,8BAA8B;AAChC;AAEA;EACE,YAAY;EACZ,yBAAyB;EACzB,0CAA0C;EAC1C,kBAAkB;EAClB,oBAAoB;AACtB;AAEA;EACE,oCAAoC;EACpC,eAAe;AACjB;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,0BAA0B;AAC5B;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,4BAA4B;AAC9B;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,cAAc;EACd,kBAAkB;EAClB,gBAAgB;EAChB,uBAAuB;AACzB;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,cAAc;EACd,gBAAgB;EAChB,uBAAuB;AACzB;AAEA;EACE,eAAe;AACjB;AAEA;EACE,gBAAgB;EAChB,cAAc;EACd,qBAAqB;EACrB,eAAe;EACf,qBAAqB;EACrB,eAAe;EACf,gBAAgB;AAClB;AAEA;EACE,cAAc;EACd,qBAAqB;AACvB;AAEA;EACE,gBAAgB;EAChB,+CAA+C;EAC/C,gCAAgC;EAChC,aAAa;EACb,mBAAmB;EACnB,kBAAkB;AACpB;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,yBAAyB;IACzB,kBAAkB;IAClB,gCAAgC;IAChC,mBAAmB;AACvB;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;AAEA;IACI,WAAW;AACf;AACA;IACI,uBAAuB;IACvB,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\nimport \"bootstrap/js/dist/dropdown\";\nimport { computed, defineAsyncComponent, getCurrentInstance, ref } from \"vue\";\nimport { IOrganization } from \"../../../../../../common/qCloudTemp/membership\";\nimport { ISignInResponse } from \"../../../../../../common/qCloudTemp/symDtoObjects\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IAuthenticationUseCase } from \"../../../../domain/useCase/IAuthenticationUseCase\";\nimport { IOrganizationGroupUseCase } from \"../../../../domain/useCase/IOrganizationGroupUseCase\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport CustomSelect from \"../../components/customSelect.vue\";\nimport AnnouncementsBar from \"../announcementsBar.vue\";\nimport ChatBot from \"../dialogs/chatbot.vue\";\n\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\nconst organizationGroupUseCase = container.resolve<IOrganizationGroupUseCase>(IOrganizationGroupUseCase);\nconst organizationUseCase = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\n\nconst user = viewModel.user!;\nconst dialog = container.resolve<IDialog>(IDialog);\nconst isBackOfficeAvailable = computed(() => !!viewModel.user?.userRoles?.find(i => i.name == \"superAdmin\"));\n\n\nconst isStatisticsPageSelected = computed(() => viewModel.studio.isStatisticsPageSelected);\nconst isManagePageSelected = computed(() => viewModel.studio.isManagePageSelected);\nconst selectedOrganizationGroup = computed(() => { return viewModel.user!.selectedOrganizationGroup });\n\nconst getOrganizations = () => {\n  if(selectedOrganizationGroup.value) {\n    if(!selectedOrganizationGroup.value?.organizationGroup.owners?.includes( user.userId)) {\n      const userOrgIds = user.organizations.map(org => org.id);\n      return selectedOrganizationGroup.value?.organizations.filter(org => userOrgIds.includes(org.id));\n    }\n\n    return selectedOrganizationGroup.value?.organizations.filter(org => org.id != selectedOrganizationGroup.value?.organizationGroup.mainOrganizationId);\n\n  }\n  return  viewModel.user?.organizations.length || 0 > 0 ? viewModel.user?.organizations.filter(org => !org.organizationGroupId) : undefined;\n  \n};\nconst organizations = computed(() => getOrganizations());\nconst selectedOrganizationId = computed(() => { return viewModel.user?.selectedOrganization?.id ?? organizations.value?.[0].id });\nconst selectedOrganizationName = computed(() => { return isStatisticsPageSelected.value || isManagePageSelected.value ? undefined : viewModel.user?.selectedOrganization?.name ?? organizations.value?.[0].name });\n\nconst isStatisticsAvailable = ref(false);\nconst ownerShipStatus = computed(() => {\n  const ownerShip = organizationGroupUseCase.checkUserIsOwner();\n  isStatisticsAvailable.value = ownerShip == \"Owner\" ? true : false;\n  return ownerShip\n});\nconst customSelect = ref(null);\n\nconst isOwner = (userId?: string) => {\n  if (!userId) {\n    return;\n  }\n  if(viewModel.user!.selectedOrganizationGroup?.organizationGroup.owners && viewModel.user!.selectedOrganizationGroup.organizationGroup.owners.includes(userId)) {\n    return true;\n  }\n  else if((viewModel.user!.selectedOrganization?.ownerUserIds && viewModel.user!.selectedOrganization.ownerUserIds.includes(userId)) || viewModel.user!.selectedOrganization?.creatorUserId === userId) {\n    return true;\n  }\n}\nfunction gotoBackOffice() {\n  goToUrl('/backoffice/dashboard');\n}\n\nfunction gotoStandAloneUIEditor() {\n  goToUrl('/standAlone/uiEditor');\n\n}\nfunction gotoWelcomePage() {\n  goToUrl('/welcome');\n\n}\n\nfunction logout() {\n  executor.execute(async () => { await authentication.signOut(); }, { loading: true, noContinueInBgBtn: true });\n}\n\nfunction goToUrl(url: string) {\n  window.open(url, \"_blank\")\n}\n\nfunction routeToStatistics() {\n  executor.execute( async () => {\n    await organizationGroupUseCase.goStatisticsPage();\n  },{loading: true, noContinueInBgBtn: true})\n  routerPush(`/organization/group-statistics/${selectedOrganizationGroup.value?.organizationGroup.ID}`)\n}\nfunction openCreateOrganizationDialog() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../../components/dialogs/organization/createOrganizationDialog.vue`)),\n    { closable: true, title: \"Create Organization\", width: \"520px\", height: \"206px\" }, {\n    onOrganizationCreated: (createdOrg: IOrganization) => {\n      (<any>customSelect.value)?.setSelected?.(createdOrg.name);\n      viewModel.user!.selectedOrganization = createdOrg;\n      getOrganizations()\n    }\n  }\n  );\n}\nfunction openJoinOrganizationGroupDialog() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../../components/dialogs/organization/joinOrganizationGroupDialog.vue`)),\n    { closable: true, title: \"Join Group Request\", width: \"658px\", height: \"549px\" },\n  );\n}\nfunction routerPush(link: string) {\n  router.push(link);\n}\nasync function onOrganizationSelect(id?: string, noRoute?: boolean) {\n  viewModel.studio.isManagePageSelected = false;\n  viewModel.studio.isStatisticsPageSelected = false;\n  const newOrgId = id;\n  if (!newOrgId) { return; }\n  await executor.execute<void>(async () => {\n    await organizationUseCase.changeOrganization(newOrgId, {noRoute});\n  }, { loading: true, noContinueInBgBtn: true });\n}\nfunction openInvitePeopleDialog() {\n  if (viewModel.user?.selectedOrganization?.accountType == \"premium\") {\n    dialog.showDialog(\n      defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n        { closable: true, title: \"Invite People to \" + selectedOrganizationName.value, width: \"658px\", maxHeight: \"700px\" },\n        {inviteCb: organizationUseCase.createInvitation.bind(organizationUseCase)}\n      );\n    }\n  else {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/premium/premiumAccountForm.vue`)),\n        { closable: true, image: {src: \"https://stechq.github.io/cdn/Idetemplates/plateauLogo-premium.svg\", width: \"134px\", height: \"40px\"},\n          width: \"1022px\", maxHeight: \"688px\", customStyle: {headerStyle: \"background: #1B4874; border-bottom: 1px solid #E3E3E3; padding: 20px; height: 80px; display: flex; align-items: center;\", closeIcon: \"color: #FFFFFF\"} }\n    );\n  }\n}\nfunction respondToInvitation(accept: boolean, invitationId: string, organizationId: string, organizationName: string) {\n    executor.execute(async () => {\n        await organizationUseCase.respondToInvitation({ accept, invitationId }, organizationId, organizationName);\n        await authentication.signIn({});\n        (<any>customSelect.value)?.setSelected?.(organizationName);\n    }, { loading: true, noContinueInBgBtn: true });\n}\nfunction deleteOrganization() {\n  const org = viewModel.user!.selectedOrganization!;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../../components/dialogs/backoffice/deleteConfirmation.vue`)),\n    { closable: true, title: \"Delete Organization\", width: \"577px\", maxHeight: \"206px\" },\n    {\n      infoText: 'Organization Name',\n      placeholder: `Write organization name '${org.name}' to delete`, validText: org.name,\n      deleteCb: async () => {\n        await executor.execute(async () => {\n          await organizationUseCase.deleteOrganization(org.id, org.name);\n          const selectOrg = organizations.value![0];\n          (<any>customSelect.value)?.setSelected?.(selectOrg);\n          close();\n        }, { loading: true });\n      }\n    },\n  );\n}\n\nfunction onHomeClick(groupChange?: boolean) {\n  viewModel.studio.isStatisticsPageSelected = false;\n  viewModel.studio.isManagePageSelected = false;\n  if(groupChange) {\n    const selectOrg = organizations.value![0];\n    (<any>customSelect.value)?.setSelected?.(selectOrg);\n  }\n  else {\n    (<any>customSelect.value)?.setSelected?.(viewModel.user?.selectedOrganization);\n  }\n  router.push('/main/applicationList/' + selectedOrganizationId.value);\n}\n\nasync function onGroupSelect(groupId: string) {\n  await executor.execute<void>(async () => {\n    await organizationGroupUseCase.changeGroup(groupId, {});\n    onHomeClick(true);\n  }, { loading: true, noContinueInBgBtn: true });\n}\n\nfunction onOtherGroupSelect() {\n  viewModel.user!.selectedOrganizationGroup = undefined;\n  executor.execute(async () => {\n    const selectOrg = organizations.value![0];\n    await organizationUseCase.changeOrganization(selectOrg.id, {noRoute: true});\n  },{ loading: true, noContinueInBgBtn: true })\n  onHomeClick(true);\n}\n\nfunction onManageOrganizationClick() {\n  routerPush(`/main/manage/${selectedOrganizationId.value}`)\n}\n\nfunction onManageGroupClick() {\n  viewModel.studio.isManagePageSelected = true;\n  viewModel.studio.isStatisticsPageSelected = false;\n  routerPush(`/organization/group/${selectedOrganizationGroup.value?.organizationGroup.ID}`)\n\n}\nfunction checkOrgsWithoutGroup() {\n  return viewModel.user!.organizations.some(org => !org.organizationGroupId)\n}\n</script>\n<template>\n  <div class=\"studio-header\">\n    <AnnouncementsBar></AnnouncementsBar>\n    <div class=\"page-content\">\n      <div class=\"header\">\n        <div class=\"col-xl-2\">\n          <img v-if=\"viewModel.user?.selectedOrganization?.accountType === 'premium'\"\n            src=\"../../../../images/plateau_logo-premium.svg\" alt=\"\" width=\"314\" height=\"50\" class=\"logo\" />\n          <img v-else src=\"../../../../images/plateau_logo-navy.svg\" alt=\"\" width=\"205\" height=\"36\" />\n        </div>\n\n        <div class=\"col-xl-8\">\n          <a class=\"documantationsText\" href=\"https://docs.onplateau.com\" target=\"_blank\">Documentation</a>\n          <a style=\"padding-left: 34px\" href=\"https://www.youtube.com/@PlateauStudio\" target=\"_blank\"\n            class=\"documantationsText\">Academy</a>\n          <a style=\"padding-left: 34px\" href=\"https://discourse.softtech.com.tr/\" target=\"_blank\"\n            class=\"documantationsText\">Discourse</a>\n        </div>\n\n        <div class=\"navbar-icons col-xl-2\" style=\"display: flex;\">\n          <div style=\"margin-left: auto;\">\n            <ChatBot />\n          </div>\n          <div class=\"col-xl-1 p-0 dot-icon\" style=\"display: flex\">\n            <img src=\"../../../../images/profile-image-dark-header.svg\" class=\"profile-icon\" style=\"margin-right: 4px;\"\n              id=\"dropdownMenuButton1\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"\n              data-bs-target=\"#dropdown-menu\" />\n            <i class=\"mdi mdi-chevron-down chevronIcon\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"\n              data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\"\n              style=\"padding-top: 10px; margin-right: -11px; font-size: 16px\"></i>\n            <ul style=\"width: 306px\" class=\"dropdown-menu user-info-dropwdown\" aria-labelledby=\"dropdownMenuButton1\">\n              <li>\n                <div class=\"user-info\">\n                  <img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 12px;\" />\n                  <div style=\"display: grid;\">\n                    <span class=\"user-name\">{{ user.name + ' ' + user.lastName }}</span>\n                    <span class=\"user-email\">{{ user.email }}</span>\n                  </div>\n                </div>\n              </li>\n              <div v-if=\"checkOrgsWithoutGroup() || selectedOrganizationGroup\" class=\"user-menu-border\"></div>\n              <li v-for=\"group in viewModel.user?.organizationGroups\" :key=\"group.ID\">\n                <div class=\"menuItem\" @click=\"onGroupSelect(group.ID)\"\n                  style=\"display: flex; justify-content: space-between;\">\n                  <div style=\"display: flex; align-items: center;\">\n                    <div class=\"group-image\">\n                      <i class=\"mdi mdi-domain\" style=\"font-size: 20px; margin-right: 0;\"></i>\n                    </div>\n                    <div style=\"display: grid;\">\n                      <span class=\"user-name\">{{ group.name }}</span>\n                      <span class=\"user-email\">{{ group.owners?.find(id => id == user.userId) ? \"Owner\" : \"User\"\n                        }}</span>\n                    </div>\n                  </div>\n                  <div>\n                    <i v-if=\"selectedOrganizationGroup?.organizationGroup.ID == group.ID\"\n                      class=\"mdi mdi-check group-check-icon\"></i>\n                  </div>\n                </div>\n              </li>\n              <li v-if=\"checkOrgsWithoutGroup()\">\n                <div class=\"menuItem\" @click=\"onOtherGroupSelect\"\n                  style=\"display: flex; justify-content: space-between;\">\n                  <div style=\"display: flex; align-items: center;\">\n                    <div class=\"group-image\">\n                      <i class=\"mdi mdi-domain\" style=\"font-size: 20px; margin-right: 0;\"></i>\n                    </div>\n                    <div style=\"display: grid;\">\n                      <span class=\"user-name\">Other</span>\n                      <span class=\"user-email\">Owner</span>\n                    </div>\n                  </div>\n                  <div>\n                    <i v-if=\"!selectedOrganizationGroup\" class=\"mdi mdi-check group-check-icon\"></i>\n                  </div>\n                </div>\n              </li>\n              <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\"\n                class=\"user-menu-border\"></div>\n              <li v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\">\n                <div class=\"menuItem\" @click=\"openJoinOrganizationGroupDialog\">\n                  <img src=\"../../../../images/office-building-p